import React from 'react'

import {
  BrowserRouter as Router,
  Match,
  Link,
} from 'react-router'

import {
  Grid,
  Navbar,
} from 'react-bootstrap'

import { compose } from 'recompose'

import Portfolio from './portfolio'

import getProjects from './projects'

const {
  Header,
  Brand,
} = Navbar

const rootPath = '/'

const projects = getProjects(`${rootPath}`)

const App = () => (
  <Router basename="/free-code-camp-projects">
    <Grid fluid style={{ height: '100%' }}>
      <Navbar
        fluid
        fixedTop
        style={{
          backgroundColor: '#00695C',
          backgroundImage: 'none',
        }}
      >
        <Header>
          <Brand>
            {
              ['about', 'contact', 'freecodecamp-projects'].map((href, index) => (
                <span key={href} style={{ color: 'white' }} >
                  { index !== 0 && ' | ' }
                  <a href={`#${href}`} style={{ color: 'white' }} >
                    { href }
                  </a>
                </span>
              ))
            }
          </Brand>
        </Header>
      </Navbar>
      <Match
        exactly pattern={ rootPath }
        render={ (props) => ( <Portfolio {...props} projects={ projects } /> ) }
      />
      {
        projects.map(({ pattern, ...route }, i) => (
          <Match
            key={`${pattern}-${i}`}
            pattern={ pattern }
            render={(props) => (
              <div style={{ marginTop: 60, height: '100%' }}>
                <div style={{ marginBottom: 15, marginTop: 10 }}>
                  <Link to="/">Home</Link>
                </div>
                <route.Component {...props} />
              </div>
            )}
          />
        ))
      }
    </Grid>
  </Router>
)


export default compose()(App)



